import React, { Component } from 'react';
import './Toolbar.scss'
class Toolbar extends Component {
  constructor(props){
    super(props);
    this.state = {activeFilter: 'none'};
    this.setThumbnailConfig = this.setThumbnailConfig.bind(this);
  }
  refresh(){
    window.location.reload();
  }
  setThumbnailConfig(item){
    if (this.props.setThumbnailConfig) this.props.setThumbnailConfig({filterName: item});
    this.setState({activeFilter: item});
  }
  render() {
    const filterName = [
      "blur", "brightness", "contrast", "shadow",
      "grayscale", "huerotate", "invert", "opacity",
      "saturate", "sepia"
    ];

    var setThumbnailConfig = this.setThumbnailConfig;
    var thisState = this.state;
    return (
      <div className='ToolbarComponent'>
        <div className='ToolbarFilters'>
          {filterName.map(function(item, i){
            var buttonClass = "btn btn-success filterButton";
            if (item == thisState.activeFilter) {
              buttonClass = "btn btn-danger filterButton";
            }
            return <button className={buttonClass}
              key = {i}
              onClick={e=>{
                setThumbnailConfig(item);
               }}
              >{item}</button>
          })}
        </div>
        <div className='ToolbarControls'>
          <button className='btn btn-primary' onClick={this.refresh}>Reset</button>
            <button className='btn btn-primary' onClick={e=>{
                if (this.props.setThumbnailConfig){
                  this.props.setThumbnailConfig("open");
                }
              }}>Open</button>
          <button className='btn btn-primary' onClick={e=>{
              if (this.props.setThumbnailConfig){
                this.props.setThumbnailConfig("save");
              }
            }}>Save</button>
        </div>
      </div>
    );
  }
}
export default Toolbar;



