import React, { Component } from 'react';
import FileSaver from 'file-saver';
import html2canvas from 'html2canvas';
import './Thumbnail.scss';

class Thumbnail extends Component {
  constructor(props){
    super(props);
    var current = new Date();
    this.state={
      imgSrc: 'https://picsum.photos/800/600?random&t=' + current.getTime(),
      filterName: 'none',
      filterConfig: '""'
    }
    this.setConfig = this.setConfig.bind(this);
  }
  setConfig(e){
    if (e==="open"){
      this.refs.openFile.click();
      var openFile = document.getElementById('openFile')
      openFile.onchange = (e) => {
        let reader = new FileReader();
        let file = e.target.files[0];
        reader.onloadend = () => {
          this.setState({
            file: file,
            imgSrc: reader.result
          });
        }
        reader.readAsDataURL(file);
      }
    }

    if (e==="save"){
      var canvasDiv = this.refs["canvasImage"];
      canvasDiv.toBlob(function(blob) {
        FileSaver.saveAs(blob, "pretty-image.png");
      },"image/png");
    }

    if (e.filterName){
      this.setState({filterName: e.filterName});
    }
  }

  componentDidMount() {
    this.updateCanvas();
  }

  updateCanvas() {
      const canvasDiv = this.refs["filterContainer"];
      const style = window.getComputedStyle(canvasDiv);
      const filterName = style.getPropertyValue('filter');

      const canvasImage = this.refs.canvasImage;
      const context = this.refs.canvasImage.getContext('2d');

      this.setState({filterConfig: filterName});

      var imageObj = new Image();
      imageObj.setAttribute('crossOrigin', 'anonymous');
      imageObj.onload = function() {
        canvasImage.width = this.width;
        canvasImage.height = this.height;
        context.clearRect(0,0,this.width, this.height);
        context.filter = filterName;
        context.drawImage(this, 0, 0);
      };

      imageObj.src = this.state.imgSrc;
  }

  componentDidUpdate(prevProps, prevState){
    if ((prevState.imgSrc !== this.state.imgSrc) || (prevState.filterName !== this.state.filterName)){
      this.updateCanvas();
    }
  }

  render() {
    const filterContainer = 'filterContainer ' + this.state.filterName;
    return (
      <div className='ThumbnailComponent'>
        <input type="file" id="openFile" ref="openFile" accept=".jpg,.png" style={{display:"none"}}/>
        <div className="thumbnailContainer" id="thumbnailContainer" ref="thumbnailContainer" >
          <div className="filterName">filter: {this.state.filterConfig}</div>
          <canvas className="canvasImage" id="canvasImage" ref="canvasImage" ></canvas>
        </div>
        <div className={filterContainer} id="filterContainer" ref="filterContainer" ></div>
      </div>
    );
  }
}
export default Thumbnail;



