import React from 'react'
import Layout from './layout'
import Toolbar from './toolbar'
import Thumbnail from './thumbnail'
import Header from './header'
import ReactDOM from 'react-dom'

import 'bootstrap/scss/bootstrap.scss';
import './App.scss'
class App extends React.Component {
  constructor(){
    super();
    this.setThumbnailConfig = this.setThumbnailConfig.bind(this);
  }
  setThumbnailConfig(e){
    this.refs.thumbnailChild.setConfig(e);
  }
  render () {
    return (
      <div className='AppComponent'>
        <Header></Header>
        <Layout>
          <Thumbnail ref='thumbnailChild' ></Thumbnail>
        </Layout>
        <Toolbar setThumbnailConfig={this.setThumbnailConfig}></Toolbar>
      </div>
    )
  }
}
export default App



